@font-face {
    font-family: 'Roboto Condensed';
    src: url('../fonts/RobotoCondensed-Regular.woff2') format('woff2'), url('../fonts/RobotoCondensed-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal; }

@font-face {
    font-family: 'PT Mono';
    src: url('../fonts/PTMono-Regular.woff2') format('woff2'), url('../fonts/PTMono-Regular.woff') format('woff');
    font-weight: normal;
    font-style: normal; }

@font-face {
    font-family: 'Roboto Condensed';
    src: url('../fonts/RobotoCondensed-Bold.woff2') format('woff2'), url('../fonts/RobotoCondensed-Bold.woff') format('woff');
    font-weight: bold;
    font-style: normal; }

$main-blue: #00b2ff;
$main-black: #1b2d37;

@mixin size($width, $height: $width) {
  width: $width;
  height: $height; }

body {
  margin: 0;
  padding: 0;
  min-width: 1440px;
  font-size: 16px;
  line-height: 22px;
  font-family: 'PT Mono', 'Courier New', monospace;
  background-color: #ffffff;
  color: #666666; }

.container {
  width: 1440px;
  margin: 0 auto; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

a {
  color: inherit;
  text-decoration: none; }

ul {
  list-style: none;
  padding: 0; }

.page-header {
  background-image: url('../img/header-bg.jpg');
  background-color: #1b2d37;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-top: 33px;
  color: #ffffff;
  min-height: 878px; }

.header-nav-wrapper {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  align-items: center; }

.header-logo {
  @include size(202px, 52px);

  img {
    @include size(202px, 52px); } }

.header-contacts {
  margin-left: auto;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-wrap: wrap;
  width: 441px;

  &-item {
    margin-right: 88px;
    padding-left: 40px;
    line-height: 18px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      background-repeat: no-repeat; }

    &:last-child {
      margin-right: 0; } } }

.contact-email::before {
  background-image: url('../img/envelope.svg');
  @include size(19px, 14px); }

.contact-phone::before {
  background-image: url('../img/phone-call.svg');
  @include size(18px); }

.btn {
  display: inline-block;
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
  font-size: 14px;
  font-weight: bold;
  width: 180px;
  padding: 15px;
  vertical-align: middle;
  text-align: center;
  text-transform: uppercase;
  word-wrap: break-word;
  border: 1px solid #ffffff;
  border-radius: 5px;
  box-sizing: border-box;
  letter-spacing: 0.05em;
  line-height: 16px; }

.btn-call {
  margin-left: 40px; }

.promo-wrapper {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex; }

.promo-text-column {
  width: 720px; }

.promo-title {
  font-family: 'Roboto Condensed', 'Arial', sans-serif;
  font-weight: bold;
  font-size: 80px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  margin-top: 250px; }

.promo-text {
  margin-top: 60px;
  font-size: 22px;
  line-height: 34px;
  padding-left: 96px;
  position: relative;

  &::before {
    content: '';
    @include size(66px, 2px);
    margin-right: 30px;
    background-color: #ffffff;
    position: absolute;
    left: 0;
    top: 17px; } }

.btn-promo {
  width: 407px;
  background-color: $main-blue;
  color: #ffffff;
  border-color: $main-blue;
  font-size: 16px;
  line-height: 19px; }

.promo-img-column {
  @include size(705px, 515px);
  margin-top: 185px;

  img {
    @include size(705px, 515px); } }

.advantages {
  padding-top: 110px; }

.advantages-list {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-wrap: wrap;
  width: 1170px;
  margin: 0 auto;
  margin-bottom: -30px; }

.advantages-item {
  border: 1px solid #ebebeb;
  border-left: 3px solid $main-blue;
  width: 570px;
  box-sizing: border-box;
  padding: 45px;
  margin-right: 30px;
  margin-bottom: 30px;

  &:nth-child(even) {
    margin-right: 0; }

  &::before {
    content: '';
    background-image: url('../img/advantages.png');
    background-repeat: no-repeat;
    display: block; }

  &-title {
    font-family: 'Roboto Condensed';
    font-size: 24px;
    line-height: 34px;
    font-weight: normal;
    color: $main-blue; } }

.operational-terms::before {
  background-position: -5px -5px;
  @include size(30px, 36px); }

.major-suppliers::before {
  background-position: -45px -5px;
  @include size(35px); }

.complete-privacy::before {
  background-position: -90px -5px;
  @include size(36px, 31px); }

.cost-customization::before {
  background-position: -136px -5px;
  @include size(35px); }

.about-us {
  padding-top: 110px; }

.about-us-wrapper {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  width: 1194px;
  margin: 0 auto; }

.about-us-img-column {
  @include size(210px, 317px);

  img {
    @include size(210px, 317px); } }

.about-us-text-column {
  width: 984px; }

.about-us-title {
  font-family: 'Roboto Condensed';
  font-size: 36px;
  line-height: 51px;
  font-weight: normal;
  color: #666666;
  margin-top: 0;
  margin-bottom: 18px; }

.btn-about-us {
  width: 180px;
  color: $main-blue;
  border-color: $main-blue; }




.feedback {
  background-color: $main-blue; }


.page-footer {
  color: $main-black; }

.footer-wrapper {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  padding-top: 65px; }

.footer-left {
  margin-right: 210px; }

.footer-logo {
  @include size(216px, 52px);

  img {
    @include size(216px, 52px); } }

.social {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-wrap: wrap;
  width: 216px;
  margin-top: 55px;

  &-item {
    margin-right: 16px;

    &:last-child {
      margin-right: 0; } } }

.social-btn {
  display: block;
  @include size(28px);
  vertical-align: middle;
  background-image: url('../img/social.png');
  background-repeat: no-repeat;
  font-size: 0;

  &-fb {
    background-position: 0 0; }

  &-ok {
    background-position: -42px 0; }

  &-vk {
    background-position: -84px 0; } }

.footer-center {
  margin-right: 230px; }

.footer-title {
  margin-bottom: 30px;
  font-family: 'Roboto Condensed';
  font-size: 15px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase; }

.map {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  flex-wrap: wrap;
  width: 520px;

  &-item {
    min-width: 200px;
    margin-right: auto;
    margin-bottom: 8px;
    opacity: 0.5;

    &:nth-child(2n) {
      margin-right: 0; } } }

.footer-contacts-item {
  margin-bottom: 8px;
  opacity: 0.5; }

.credits {
  margin-top: 55px;
  border-top: 1px solid rgba(98, 105, 98, 0.2); }

.credits-list {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  align-items: center; }

.credits-item {
  opacity: 0.5;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;

  &:nth-child(1) {
    margin-right: 60px; }

  &:nth-child(2) {
    margin-right: 145px; }

  &:nth-child(3) {
    margin-right: 405px; } }
